.add-form{
    display: block;
    margin: 0 auto;
    width: 50%;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px #ccc;
}

.form{
    display: flex;
    flex-direction: column;
}

.form label{
    font-size: 20px;
    margin: 10px 0;
}

.form input{
    width: 100%;
    height: 30px;
    font-size: 20px;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.form input:focus{
    outline: none;
}

.form button{
    width: 100%;
    height: 50px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    margin: 10px 0;
}

.form button:hover{
    background-color: #0069d9;
}

.form button:active{
    background-color: #0056b3;
}

.form button:focus{
    outline: none;
}
