  .App {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    text-align: center;
    width: 100%;
  }

  .App h1 {
    border-bottom: 1px solid black;
    margin: 10px;
  }

  .buttons {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px;
  }

  .btn {
    background-color: #4CAF50;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 20px;
    height: 50px;
    margin: 10px;
    width: 200px;
  }

  .btn:hover {
    background-color: #45a049;
  }

  .btn:active {
    background-color: #3e8e41;
  }

  .btn:focus {
    outline: none;
  }

  .add-book {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
  }

  .show-books {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
  }

  .show-bookss {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
  }

  .books {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 50%;
  }