.book{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border : 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px #ccc;
    margin: 10px;
    padding: 10px;
}

.book h3{
    margin: 10px;
    font-weight: bold;
    font-size: 30px;
}

.book p{
    margin: 8px;
    font-size: 20px;
}

.book button{
    width: 100%;
    height: 50px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
    margin: 10px 0;
}

.book button:hover{
    background-color: #45a049;
}

.book button:active{
    background-color: #3e8e41;
}

.book img{
    width: 200px;
    height: 200px;
    margin: 10px;
}